.foter {
  position: absolute;
  top: 1300px;
  width: 100%;
  border-top: 4px solid rgb(20, 20, 32);
  padding-top: 15px;
}
.first {
  display: flex;
  width: 250px;
  justify-content: space-between;
  float: right;
}
.middle {
  padding-left: 20px;
  padding-right: 20px;
  border-left: 1px solid black;
  border-right: 1px solid black;
}
.second {
  display: flex;
  position: relative;
  bottom: -25px;
  justify-content: space-between;
  width: 100%;
}
.second > img {
  margin-left: 200px;
  object-fit: contain;
}
.second > p {
  color: rgb(92, 92, 92);
  text-align: right;
  width: 320px;
}
